	module.exports = {"data":{"markdownRemark":{"html":"<p>The caravans to the Northern Waste have recently set out a call for help.\nThe village on the Icebay of Forochel has been unreachable and all\ncommunication has ceased. A band of adventurers is needed to solve the mystery\nof the village for glory or loot.  This is a land of freezing cold and\nmonstrous trolls, so only the brave or stupid need apply. A dwarven caravan\nleaves from the ranger camp west of Vinyanost and a goblin caravan has been\nspotted leaving the hills to the north.</p>\n<p>OOC:  This zone is based on the Icebay of Forochel at the northern\nend of the Misty Mountains.  <a href=\"http://www.tuckborough.net/lands.html\">http://www.tuckborough.net/lands.html</a> is\na good site if you're interested in some history.  Read the section\nabout Forodwaith.</p>\n<p>There are two mobs and one weapon that are not allowed to leave the\nzone.  Mechanics are in place to ensure this does not happen, but as\nwe have seen in the past resourceful RoTS players have tried to find\nways to go around mechanics.  Anyone caught with the axe or following\ndwarves out of the zone will be punished HARSHLY.  This is your only\nwarning.</p>\n<p>That being said, have fun and use caution.. it is a dangerous zone!</p>\n<p><strong>Credits:</strong></p>\n<p>Lowtax: overall design, scripting, mobs, objects</p>\n<p>Turamarth, Adunakhor, Rathmanken: helping</p>","frontmatter":{"title":"News 1610","date":"October 09, 2017","author":"Azriya","desc":"The Northern Waste"}}},"pathContext":{"path":"/news/1610"}}

